export default {
  name: 'mb',
  data (){
    return {
      selected2: 1,
      options2: [
        {
          label: '我不想买了',
          value: 1
        },
        {
          label: '信息填写错误，重新拍',
          value: 2
        },
        {
          label: '卖家缺货',
          value: 3,
        },
        {
          label: '同城见面交易',
          value: 4,
        },
        {
          label: '其他原因',
          value: 5,
        }
      ]
    }
  },
  methods: {
    //取消订单
    closeSelected(){
      this.$emit('changeSelected',false)
    },
    handleClickCancel(){
      this.$emit('changeDeleteOrder',this.selected2)
