export default {
  name: 'OrderData',
  data (){
    return {
      checked: false,
      option: {
        label: '',
        value: '0',
        disabled: false
      }
    }
  },
  props: {
    list: Array,
    type: Number
  },
  methods: {
    //查看物流
    lookWl(title,send){
      this.$createDialog({
        type: 'alert',
        title: '物流信息',
        content: '<p style="line-height:2;">快递公司：'+title+'</p>'+'<p style="line-height:2;">快递单号：'+send+'</p>',
        icon: ''
      }).show()
    },
    //去付款
    payClick(id,index){
      this.$emit('changePayClick',id,index)
    },
    //取消
    confirmClick(id,index){
      console.log(id)
      this.$emit('deleteOrder',id,index)
    },
    //确认收货
    confirmOrder(id,index){
      this.$emit('confirmGoodsOrder',id,index)
    }
  },
  mounted (){
  }
}



